.body {
  /*border: 3px solid black;*/
  height: 350px;
  width: 350px;
  margin-top: 100px;
  margin-left: auto;
  margin-right: auto;
  /*transform: rotate(-45deg);*/
}

.container {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.container1 {
  /*benchmark for other positioning*/
}

.container2 {
  -webkit-transform: rotate(-90deg);
  -ms-transform: rotate(-90deg);
  transform: rotate(-90deg);
  right: 150%;
  bottom: 79%;
  background-position: center;

  -webkit-transition: opacity 15s ease-in-out;
  -o-transition: opacity 15s ease-in-out;
  transition: opacity 15s ease-in-out;
}

.container3 {
  -webkit-transform: rotate(90deg);
    -ms-transform: rotate(90deg);
    transform: rotate(90deg);
  bottom: 114%;
  left: 38%;
}

.container4 {
  -webkit-transform: rotate(180deg);
    -ms-transform: rotate(180deg);
    transform: rotate(180deg);
  bottom: 193%;
  right: 113%;
}

.container5 {
  left: 153%;
  top: 16%;
  width: 117%;
}

.container6 {
  -webkit-transform: rotate(90deg);
    -ms-transform: rotate(90deg);
    transform: rotate(90deg);
    top: 54%;
    left: 85%;
}

.container7 {
  -webkit-transform: rotate(180deg);
    -ms-transform: rotate(180deg);
    transform: rotate(180deg);
    bottom: 77%;
    right: 290%;
}


.container8 {
  -webkit-transform: rotate(-90deg);
    -ms-transform: rotate(-90deg);
    transform: rotate(-90deg);
  bottom: 315.5%;
  right: 204%;
}


.art {
  height: 100%;
  width: 100%;
  background-repeat: no-repeat;
}

.art2 {
  height: 100%;
  width: 100%;
  background-position: center;
  background-repeat: no-repeat;
}

@media only screen and (max-width: 700px) {
    .body {
      height: 100px;
      width: 100px;
        /*background-color: lightblue;*/
    }
}

@media only screen and (max-width: 550px) {
  .body {
    margin-top: 60px;
    height: 100px;
    width: 100px;
  }
}

.rotate-scale-up {
	-webkit-animation: rotate-scale-up 500s linear infinite alternate both;
	        animation: rotate-scale-up 500s linear infinite alternate both;
}


@-webkit-keyframes rotating-function {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


@keyframes rotating-function {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@-webkit-keyframes turnit {
    from {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
    to {-webkit-transform: rotate(360deg);transform: rotate(360deg);}
}

@keyframes turnit {
    from {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
    to {-webkit-transform: rotate(360deg);transform: rotate(360deg);}
}
@-webkit-keyframes turnitOtherWay {
    from {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
    to {-webkit-transform: rotate(-360deg);transform: rotate(-360deg);}
}
@keyframes turnitOtherWay {
    from {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
    to {-webkit-transform: rotate(-360deg);transform: rotate(-360deg);}
}


@-webkit-keyframes rotate-scale-up {
  0% {
    -webkit-transform: scale(2) rotateZ(0);
            transform: scale(2) rotateZ(0);
  }
  50% {
    -webkit-transform: scale(3) rotateZ(180deg);
            transform: scale(3) rotateZ(180deg);
  }
  100% {
    -webkit-transform: scale(2) rotateZ(360deg);
            transform: scale(2) rotateZ(360deg);
  }
}
@keyframes rotate-scale-up {
  0% {
    -webkit-transform: scale(2) rotateZ(0);
            transform: scale(2) rotateZ(0);
  }
  50% {
    -webkit-transform: scale(3) rotateZ(180deg);
            transform: scale(3) rotateZ(180deg);
  }
  100% {
    -webkit-transform: scale(2) rotateZ(360deg);
            transform: scale(2) rotateZ(360deg);
  }
}



